.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;

  @media (max-width: $vp-1023) {
    padding: 0 45px;
  }

  @media (max-width: $vp-767) {
    padding: 0 15px;
  }

  &--description {
    display: flex;
    justify-content: space-between;

    @media (max-width: $vp-767) {
      display: block;
    }
  }

  &--footer {
    display: flex;

    @media (max-width: $vp-767) {
      flex-direction: column;
    }
  }
}
